import React, { useState } from "react";
import QrReader from "react-qr-reader";
import { useNavigate } from "react-router-dom";

export default function ScanTable({ history }) {
  let navigate = useNavigate();
  const [cameraPermission, setCameraPermission] = useState(false);

  const validScan = (scanText) => {
    return true;
  };

  navigator.permissions.query({name: "camera"}).then((res) => {
    if(res.state === "granted"){
      setCameraPermission(true);
    }
  })

  const handleScan = (result) => {
    if (result) {
      console.log("Scanning Result", result);
      if (validScan(result)) {
        navigate(
          `${process.env.PUBLIC_URL}/menu?hotelid=Mc%20Donalds&table=3`,
          { replace: true }
        );
      }
    }
  };

  const handleError = (err) => {
    console.error("Error in Scanning", err);
  };

  return (
    <div className="container">
      <br />
      <br />
      <center>
        <h1>Please Scan the QR Code present on the table</h1>
        {!cameraPermission && (
          <div>
            <p>
              We would require your camera permission to scan the QR code
              present on the table Please grant us the access when prompted
            </p>
            <button
              className="btn btn-primary"
              onClick={() => {
                setCameraPermission(true);
              }}
            >
              Scan Now
            </button>
          </div>
        )}
        {cameraPermission && (
          <div>
            <QrReader
              delay={300}
              onError={handleError}
              onScan={handleScan}
              style={{ width: "80%" }}
            />
            <br />
            <br />
            <button
              onClick={() => {
                handleScan("something");
              }}
            >
              Skip QR
            </button>
            <button onClick={() => { navigate(`${process.env.PUBLIC_URL}/insert`) }}>Insert items</button>
          </div>
        )}
      </center>
    </div>
  );
}
