import React, { useState } from "react";
import MenuItem from "./MenuItem";

export default function SearchCanvas({ items }) {
  const searchBoxStyle = {
    width: "100%",
    borderRadius: "10px",
    padding: "2%",
    borderOpacity: 0.5,
  };

  const [menu, setMenu] = useState(items);
  console.log(menu);

  const searchTrigger = (query) => {
      console.log(query.target.value);
      items = items.filter((item) => (item.name.toUpperCase().includes(query.target.value.toUpperCase())));
      setMenu(items);
  }

  return (
    <div>
      <input
        type="text"
        placeholder="Search Within the Menu"
        style={searchBoxStyle}
        autoFocus
        onChange={searchTrigger}
      />
      <br />
      <br />

      <div className="d-flex mb-2">
        <div className="flex-fill form-check form-switch">
          <input className="form-check-input" type="checkbox" />
          <label className="form-check-label">Veg</label>
        </div>
        <div className=" flex-fill form-check form-switch">
          <input className="form-check-input" type="checkbox" />
          <label className="form-check-label">Non-Veg</label>
        </div>
      </div>
      <hr />
      {menu && menu.map((items,id) => (
          <MenuItem item={items} key={id} visible={true} />
      ))}
    </div>
  );
}
