import React from 'react';
import './App.css';
import { Route, Routes} from 'react-router-dom';
import Home from './components/Home';
import ScanTable from './components/ScanTable';
import Menu from './components/Menu';
import InsertItems from './components/InsertItems';

function App() {
  return (
    <React.Fragment>
      <Routes basename={"/meta_menu_staging"}>
        <Route path={`${process.env.PUBLIC_URL}/`} exact element={<Home />} />
        <Route path={`${process.env.PUBLIC_URL}/scan-table`} element={<ScanTable />} />
        <Route path={`${process.env.PUBLIC_URL}/menu`} exact element={<Menu />} />
        <Route path={`${process.env.PUBLIC_URL}/insert`} exact element={<InsertItems />} />
      </Routes>
    </React.Fragment>
  );
}

export default App;
