import React,{useState} from "react";

export default function Item({itemName, poster}) {

  const [count, setCount] = useState(1);

  return (
    <div>
      <center>
        <h1>Viewing {itemName}</h1>
        <div className="model-div">
          <model-viewer
            alt="Neil Armstrong's Spacesuit from the Smithsonian Digitization Programs Office and National Air and Space Museum"
            src={`assets/models/astraunot.glb`}
            ar
            ar-modes="webxr scene-viewer quick-look"
            auto-rotate
            auto-rotate-delay="1000"
            rotation-per-second="90deg"
            seamless-poster
            shadow-intensity="1"
            camera-controls
            poster={poster}
          ></model-viewer>
        </div>
        <br />
        <button className="btn btn-outline-primary" onClick={() => {setCount(count-1 > 0 ? count-1 : 1)}}>-</button>
        <span style={{height: "38px", width:"40px", marginLeft:"1em", marginRight:"1em"}} >{count}</span>
        <button className="btn btn-outline-primary" onClick={() => {setCount(count+1)}}>+</button>&nbsp;&nbsp;
        <button data-bs-dismiss="offcanvas" className="btn btn-primary">Add</button>
      </center>
    </div>
  );
}
