import React, {useEffect, useState} from 'react'
// import {collection, doc, getDoc, getDocs, query, writeBatch} from 'firebase/firestore';
import { db } from '../configs/firebase';

export default function InsertItems() {

    var password = "test123" || prompt("Enter password");

    const [hotels, setHotels] = useState([]);

    const getHotels = async () => {
        // const docRef = query(collection(db, "hotel"));
        // const docSnap = await getDocs(docRef);
        var hotels = [];
        // docSnap.forEach((snap) => {
        //     hotels.push(snap.data());
        // });
        // console.log(hotels);
        setHotels(hotels);
    }

    const processFile = async (data) => {
        var batch = db.batch();
        var lines = data.split("\n");
        lines.shift(0);
        var items = [];
        lines.forEach((line) => {
            if(line){
                var words = line.split(',');
                var itemObj = {
                    name: words[0],
                    price: words[1],
                    short_description: words[2],
                    detailed_description : words[3],
                    isveg : words[4] === 1 ? true : false,
                    modelurl: words[5],
                    imgurl: words[6],
                    tags: words[7].split(";"),
                    categorynames: words[8].split(";")
                }
                items.push(itemObj);
                console.log(itemObj);
            }
        });
        items.forEach(async (item) => {
            const collectionRef = await db.collection("item").doc();
            batch.create(collectionRef, item);
        });
        await batch.commit();
    }

    const submitData = (e) => {
        e.preventDefault();
        var reader = new FileReader();
        reader.onload = (res) => {
            processFile(res.target.result);
        };
        reader.readAsText(new Blob(e.target[0].files));
    }

    useEffect(() => {
        getHotels();
    }, [])

    return (
        <div>
            {password === "test123" && <div>
                <br/><br/><br/>
                    <center>
                    <select>
                        {hotels.map((hotel, id) => (
                            <option key={id}>{hotel.name}</option>
                        ))}
                    </select>
                    </center>
                   <form onSubmit={submitData}>
                        <center>
                        <input type="file"  />
                        <button type='submit'>Submit</button>
                        </center>
                   </form>
                </div>}
            {password !== "test123" && <div>
                    <h1>Access denied Get out</h1>
                </div>}
        </div>
    )
}
